.site__logo {
  width: 100%;
  padding: 1rem;
}
.site__logo img {
  width: 125px;
  margin: auto;
  display: block;
}

.site__nav {
  width: 100%;
  border-top: 1px solid #FCDC17;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 1rem;
}
.site__nav .navSocials {
  width: 20%;
}
.site__nav .navSocials ul {
  list-style: none;
}
.site__nav .navSocials ul li {
  display: inline-block;
}
.site__nav .navSocials ul li:not(:first-child) {
  margin: 0 10px;
}
.site__nav .navSocials .navSocials__icon {
  width: 20px;
}
.site__nav .navigation ul {
  list-style: none;
  padding: 0;
}
.site__nav .navigation ul li {
  display: inline-block;
  color: #ffffff;
  margin: 0 10px;
  transition: 200ms ease-in-out;
  cursor: pointer;
}
.site__nav .navigation ul li:hover {
  color: #FCDC17;
}
.site__nav .navButton {
  width: 20%;
  text-align: end;
}
.site__nav .navButton .button {
  margin-left: auto;
}